.content_img {
  /* text-align: center; */
  height: 150px;
}

.content_img img {
  height: 100%;
}

.descroption_block {

  display: flex;
    align-items: center;
}

.content_text {
  color: black;
  font-weight: bold;
  font-size: 18px;
}

.mainPhoto {
  max-width: 200px;
}
.status{
  font-weight: bold;
  font-style: italic;
  text-transform: capitalize;
}



.input__wrapper {
  width: 100%;
  position: relative;
  margin: 15px 0;
  text-align: center;
}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__file_icon_wrapper {
  height: 60px;
  width: 60px;
  /* margin-right: 15px; */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-right: 1px solid #000;
}

.input__file_button_text {
  line-height: 1;
  margin-top: 1px;
  padding: 15px;
}

.input__file_button {
  border: 1px solid #000;
  /* width: 100%; */
  max-width: max-content;
  height: 60px;
  /* background: #1bbc9b; */
  background: #d7e60b;
  color: #000;
  font-size: 1.125rem;
  font-weight: 700;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  border-radius: 3px;
  cursor: pointer;
  margin: 0 auto;
}